














import {IQualityIndicator} from '@/interfaces/ChemData.ts';
import { PropType } from 'vue'

import { Chart, registerables } from 'chart.js';
import ChemModel from "@/chem_model/chem_model";
Chart.register(...registerables);

import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop, Watch } from 'vue-property-decorator';

@Component
export default class Charts extends Vue {

  @Prop() readonly chartData!: Array<IQualityIndicator>;

  private chartDataCopy: Array<IQualityIndicator> = this.chartData;
  private chartT!: any;
  private chartNu! : any;

  private drawChart(): void {
    let chartT : any = document.getElementById('chartT');
    let ctxT: any = chartT.getContext('2d');
    this.chartT = new Chart(ctxT, {
      type: 'line',
      data: {
        labels: this.chartDataCopy.map((el)=>{return el['z']}),
        datasets: [
          {
            label: 'Распределение температуры материала по длине канала',
            data: this.chartDataCopy,
            fill: false,
            borderColor: 'rgb(75, 192, 192)',
            parsing: {
              yAxisKey: 'T',
              xAxisKey: 'z'
            }
          },
        ]
      },
      options: {
        parsing: false,
        responsive: true,
        maintainAspectRatio: false,
        locale: 'ru',
        scales: {
          x: {
            beginAtZero: true,
            title: {
              display: true,
              text: 'Координата по длине канала, м'
            }
          },
          y: {
            beginAtZero: false,
            title: {
              display: true,
              text: 'Температура, °С'
            }
          }
        }
      }
    });
    let chartNu : any = document.getElementById('chartNu');
    let ctxNu: any = chartNu.getContext('2d');
    this.chartNu = new Chart(ctxNu, {
      type: 'line',
      data: {
        labels: this.chartDataCopy.map((el)=>{return el['z']}),
        datasets: [
          {
            label: 'Распределение вязкости материала по длине канала',
            data: this.chartDataCopy,
            fill: false,
            borderColor: 'rgb(195,43,86)',
            parsing: {
              yAxisKey: 'Nu',
              xAxisKey: 'z'
            }
          }
        ]
      },
      options: {
        parsing: false,
        responsive: true,
        maintainAspectRatio: false,
        locale: 'ru',
        scales: {
          x: {
            beginAtZero: true,
            title: {
              display: true,
              text: 'Координата по длине канала, м'
            }
          },
          y: {
            beginAtZero: false,
            title: {
              display: true,
              text: 'Вязкость, Па*с'
            }
          }
        }
      }
    });
  }
  public updateChart(){
    console.log('update chart');
    this.chartDataCopy = this.chartData;
    this.chartT.data = {
      labels: this.chartDataCopy.map((el)=>{return el['z']}),
      datasets: [
        {
          label: 'Зависимость температуры от координaты, °С',
          data: this.chartDataCopy,
          fill: false,
          borderColor: 'rgb(75, 192, 192)',
          parsing: {
            yAxisKey: 'T',
            xAxisKey: 'z'
          }
        },
      ]
    }

    this.chartT.update();
    this.chartNu.data = {
      labels: this.chartDataCopy.map((el)=>{return el['z']}),
      datasets: [
        {
          label: 'Зависимость взякости от координаты, Па*с',
          data: this.chartDataCopy,
          fill: false,
          borderColor: 'rgb(195,43,86)',
          parsing: {
            yAxisKey: 'Nu',
            xAxisKey: 'z'
          }
        }
      ]
    }
    this.chartNu.update();
  }
  mounted() {
    this.drawChart();
  }
  updated(){
    this.updateChart();
  }
  @Watch('chartData', {
    immediate: true, deep: true
  })
  chartDataChanged(newVal: Array<IQualityIndicator>, oldVal: Array<IQualityIndicator>) {
    this.chartDataCopy = newVal;
  }
}
