









































































































































import { Chart, registerables } from 'chart.js';
import ChemModel from "@/chem_model/chem_model";
Chart.register(...registerables);

import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop, Watch, Emit } from 'vue-property-decorator';
import {IQualityIndicator} from "@/interfaces/ChemData";

@Component
export default class InputData extends Vue{
  @Prop({default: 0.17}) W!: number;
  Wmod = this.W;
  @Prop({default: 0.05}) H!: number;
  Hmod = this.H;
  @Prop({default: 6.6}) L!: number;
  Lmod = this.L;
  @Prop({default: 890}) po!: number;
  poMod = this.po;
  @Prop({default: 2300}) c!: number;
  cMod = this.c;
  @Prop({default: 175}) T_0!: number;
  T_0mod = this.T_0;
  @Prop({default: 890}) V_u!: number;
  V_uMod = this.V_u;
  @Prop({default: 200}) T_u!: number;
  T_uMod = this.T_u;
  @Prop({default: 0.1}) zStep!: number;
  zStepMod = this.zStep;
  @Prop({default: 1550}) mu_0!: number;
  mu_0Mod = this.mu_0;
  @Prop({default: 0.015}) b!: number;
  bMod = this.b;
  @Prop({default: 180}) T_r!: number;
  T_rMod = this.T_r;
  @Prop({default: 0.4}) n!: number;
  nMod = this.n;
  @Prop({default: 2000}) alf_u!: number;
  alf_uMod = this.alf_u;
  @Prop({default: false}) newMaterial!: boolean;

  @Prop({default: false}) admin!: boolean;

  validate(value: number, min: number, max: number){
    try {
      if (isNaN(value)) {
        alert('Заданный аргумент не является числом!');
        return false;
      }
      if (value <= min || value >= max) {
        alert(`Заданный аргумент находится вне диапазона допустимых значений: (${min}: ${max})`);
        return false;
      }
    } catch {
      alert('Неверный ввод');
      return false;
    }
    return true
  }

  updateW(){
    if (this.validate(this.Wmod, 0, 1000)){
      this.updateWParent(this.Wmod);
    } else {
      this.Wmod = this.W;
    }
  }
  updateH(){
    if (this.validate(this.Hmod, 0, 1000)){
      this.updateHParent(this.Hmod);
    } else {
      this.Hmod = this.H;
    }
  }
  updateL(){
    if (this.validate(this.Lmod, 0, 1000)){
      this.updateLParent(this.Lmod);
    } else {
      this.Lmod = this.L;
    }
  }
  updatePo(){
    if (this.validate(this.poMod, 0, 10000)){
      this.updatePoParent(this.poMod);
    } else {
      this.poMod = this.po;
    }
  }
  updateC(){
    if (this.validate(this.cMod, 0, 10000)){
      this.updateCParent(this.cMod);
    } else {
      this.cMod = this.c;
    }
  }
  updateT_0(){
    if (this.validate(this.T_0mod, 0, 10000)){
      this.updateT_0parent(this.T_0mod);
    } else {
      this.T_0mod = this.T_0;
    }
  }
  updateV_u(){
    if (this.validate(this.V_uMod, 0, 100)){
      this.updateV_uParent(this.V_uMod);
    } else {
      this.V_uMod = this.V_u;
    }
  }
  updateT_u(){
    if (this.validate(this.T_uMod, 0, 1000)){
      this.updateT_uParent(this.T_uMod);
    } else {
      this.T_uMod = this.T_u;
    }
  }
  updateZstep(){
    if (this.validate(this.zStepMod, 0, 10)){
      this.updateZstepParent(this.zStepMod);
    } else {
      this.zStepMod = this.zStep;
    }
  }
  updateMu_0(){
    if (this.validate(this.mu_0Mod, 0, 100000)){
      this.updateMu_0parent(this.mu_0Mod);
    } else {
      this.mu_0Mod = this.mu_0;
    }
  }
  updateB(){
    if (this.validate(this.bMod, 0, 1.9)){
      this.updateBparent(this.bMod);
    } else {
      this.bMod = this.b;
    }
  }
  updateT_r(){
    if (this.validate(this.T_rMod, 0, 1000)){
      this.updateT_rParent(this.T_rMod);
    } else {
      this.T_rMod = this.T_r;
    }
  }
  updateN(){
    if (this.validate(this.nMod, 0, 10)){
      this.updateNParent(this.nMod);
    } else {
      this.nMod = this.n;
    }
  }
  updateAlf_u(){
    if (this.validate(this.alf_uMod, 0, 10000)){
      this.updateAlf_uParent(this.alf_uMod);
    } else {
      this.alf_uMod = this.alf_u;
    }
  }



  @Emit('update-w')
  updateWParent(W: number) {
    return;
  }
  @Emit('update-h')
  updateHParent(H: number) {
    return;
  }
  @Emit('update-l')
  updateLParent(L: number) {
    return;
  }
  @Emit('update-po')
  updatePoParent(po: number) {
    return;
  }
  @Emit('update-c')
  updateCParent(c: number){
    return;
  }
  @Emit('update-t-0')
  updateT_0parent(T_0: number){
    return;
  }
  @Emit('update-v-u')
  updateV_uParent(V_u: number) {
    return;
  }
  @Emit('update-t-u')
  updateT_uParent(T_u: number) {
    return;
  }
  @Emit('update-z-step')
  updateZstepParent(zStep: number) {
    return;
  }
  @Emit('update-mu-0')
  updateMu_0parent(mu_0: number) {
    return;
  }
  @Emit('update-b')
  updateBparent(b: number){
    return;
  }
  @Emit('update-t-r')
  updateT_rParent(T_r: number){
    return;
  }
  @Emit('update-n')
  updateNParent(n: number){
    return;
  }
  @Emit('update-alf-u')
  updateAlf_uParent(alf_u: number){
    return;
  }

  @Watch('W', {
    immediate: true, deep: true
  })
  updateWProp(newVal: number, oldVal: number) {
    this.Wmod = newVal;
  }
  @Watch('H', {
    immediate: true, deep: true
  })
  updateHProp(newVal: number, oldVal: number) {
    this.Hmod = newVal;
  }
  @Watch('L', {
    immediate: true, deep: true
  })
  updateLProp(newVal: number, oldVal: number) {
    this.Lmod = newVal;
  }
  @Watch('po', {
    immediate: true, deep: true
  })
  updatePoProp(newVal: number, oldVal: number) {
    this.poMod = newVal;
  }
  @Watch('c', {
    immediate: true, deep: true
  })
  updateCProp(newVal: number, oldVal: number) {
    this.cMod = newVal;
  }
  @Watch('T_0', {
    immediate: true, deep: true
  })
  updateT_0Prop(newVal: number, oldVal: number) {
    this.T_0mod = newVal;
  }
  @Watch('V_u', {
    immediate: true, deep: true
  })
  updateV_uProp(newVal: number, oldVal: number) {
    this.V_uMod = newVal;
  }
  @Watch('T_u', {
    immediate: true, deep: true
  })
  updateT_uProp(newVal: number, oldVal: number) {
    this.T_uMod = newVal;
  }
  @Watch('zStep', {
    immediate: true, deep: true
  })
  updateZSetpProp(newVal: number, oldVal: number) {
    this.zStepMod = newVal;
  }
  @Watch('mu_0', {
    immediate: true, deep: true
  })
  updateMu_0Prop(newVal: number, oldVal: number) {
    this.mu_0Mod = newVal;
  }
  @Watch('b', {
    immediate: true, deep: true
  })
  updateBProp(newVal: number, oldVal: number) {
    this.bMod = newVal;
  }
  @Watch('T_r', {
    immediate: true, deep: true
  })
  updateT_rProp(newVal: number, oldVal: number) {
    this.T_rMod = newVal;
  }
  @Watch('n', {
    immediate: true, deep: true
  })
  updateNProp(newVal: number, oldVal: number) {
    this.nMod = newVal;
  }
  @Watch('alf_u', {
    immediate: true, deep: true
  })
  updateAlf_uProp(newVal: number, oldVal: number) {
    this.alf_uMod = newVal;
  }

}
