





























































































































import {Component, Vue, Watch} from 'vue-property-decorator'
import ChemModel from '@/chem_model/chem_model.ts'
import Charts from '@/components/Charts.vue'
import InputData from '@/components/InputData.vue'
import Tables from "@/components/Tables.vue";

import {IQualityIndicator} from '@/interfaces/ChemData.ts';
import {getRequest, sendData} from "@/funcs/funcs";
import {domain} from "@/consts/urls";

@Component({
  components: {
    Charts,
    InputData,
    Tables
  }
})
export default class Main extends Vue{
  private chartData:  Array<IQualityIndicator> = [];
  private model: ChemModel = new ChemModel();
  private chartsKey = 0;

  private selectedMaterial = {id: 0, Tm: 'Полипропилен'};
  private materials: Array<any> = [];
  private calculateTime = 0;

  private authorized = false;
  private admin = false;

  private login = "";
  private passw = "";

  private modalUser = false;

  private newUserType = "Исследователь";
  private newUserLogin = "";
  private newUserPassword = "";

  private memoryUsed = 0;

  private addNewMaterial = false;
  private newMaterialName = "";

  exit(){
    this.authorized = false;
    this.admin = false;
  }

  updateDBModel(){
    sendData(`${domain}/update_material`, {'model': this.model})
    alert('Данные обновлены');
  }

  deleteMaterial(){
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    let self = this;
    sendData(`${domain}/delete_material`, {'model': this.model}).then(res => {
      getRequest(`${domain}/get_materials`, {}).then(res => {
      console.log(res['materials']);
      self.materials = res['materials'];
      for (let mat of self.materials){
        if (mat['Tm'] === 'Полипропилен'){
          self.selectedMaterial = mat;
        }
      }
      if (self.admin) {
        self.materials.push({'id': 666, 'Tm': 'Новый материал'});
      }
    })
    });

    alert('Материал удалён');
  }

  saveNewMaterial(){
    this.model.Tm = this.newMaterialName;
    let self = this;
    sendData(`${domain}/new_material`, {'model': this.model}).then(res => {
      this.addNewMaterial = false;
      getRequest(`${domain}/get_materials`, {}).then(res => {
        console.log(res['materials']);
        self.materials = res['materials'];
        if (self.admin) {
          self.materials.push({'id': 666, 'Tm': 'Новый материал'});
        }
        for (let mat of res['materials']){
          if (mat['Tm'] == self.newMaterialName){
            self.$nextTick(()=>{
              self.selectedMaterial = {'id': mat['id'], 'Tm': mat['Tm']};
            })
          }
        }
        this.newMaterialName = "";
      })

    });
  }

  loginIn(){
    getRequest(`${domain}/login`, {login: this.login, password: this.passw}).then(res => {
      console.log(res);
      if (!res['status']) {
        alert('Неверный логин или пароль!');
      } else {
        if (res['user']['admin']) this.admin = true;
        this.authorized = true;
        this.getMaterials();
      }
    }).catch(er => {console.error(er)});
  }

  addNewUser(){
    this.newUserType = "Исследователь";
    this.newUserLogin = "";
    this.newUserPassword = "";
    this.modalUser = true;
  }

  sendNewUser(){
    console.log(this.newUserLogin, this.newUserPassword, this.newUserType);
    sendData(`${domain}/add_user`,
        {login: this.newUserLogin, password: this.newUserPassword, type: this.newUserType}).then(res => {
          if (res['status']){
            alert('Пользователь успешно создан');
          }
          else {
            alert('Невозможно создать такого пользователя');
          }
    }).catch(er => {
      alert('Невозможно создать такого пользователя');
    })
  }


  updateW(W: string){
    this.model.W = parseFloat(W);
  }
  updateL(L: string){
    this.model.L = parseFloat(L);
  }
  updateH(H: string){
    this.model.H = parseFloat(H);
  }
  updatePo(po: string){
    this.model.po = parseFloat(po);
  }
  updateC(c: string){
    this.model.c = parseFloat(c);
  }
  updateT_0(T_0: string){
    this.model.T_0 = parseFloat(T_0);
  }
  updateV_u(V_u: string){
    this.model.V_u = parseFloat(V_u);
  }
  updateT_u(T_u: string){
    this.model.T_u = parseFloat(T_u);
  }
  updateZstep(zStep: string){
    this.model.zStep = parseFloat(zStep);
  }
  updateMu_0(mu_0: string){
    this.model.mu_0 = parseFloat(mu_0);
  }
  updateB(b: string){
    this.model.b = parseFloat(b);
  }
  updateT_r(T_r: string){
    this.model.T_r = parseFloat(T_r);
  }
  updateN(n: string){
    this.model.n = parseFloat(n);
  }
  updateAlf_u(alf_u: string){
    this.model.alf_u = parseFloat(alf_u);
  }

  getMaterials(){
    let self = this;
    getRequest(`${domain}/get_materials`, {}).then(res => {
      console.log(res['materials']);
      self.materials = res['materials'];
      for (let mat of self.materials){
        if (mat['Tm'] === 'Полипропилен'){
          self.selectedMaterial = mat;
        }
      }
      if (self.admin) {
        self.materials.push({'id': 666, 'Tm': 'Новый материал'});
      }
    })
  }

  mounted(){
    this.chartData = this.model.calcQualityIndicators();
    this.getMaterials();
  }
  @Watch('model', {
    immediate: true, deep: true
  })
  modelChanged(newVal: ChemModel, oldVal: ChemModel) {
    this.memoryUsed = 0;
    let t1 = new Date().getTime();
    this.chartData = newVal.calcQualityIndicators();
    this.chartsKey++;
    let t2 = new Date().getTime();
    this.calculateTime = t2-t1;
    if (this.calculateTime === 0) this.calculateTime = 1;
    // @ts-ignore
    this.memoryUsed = Math.round(performance.memory.totalJSHeapSize / 1024 / 1024);
  }

  @Watch('selectedMaterial', {
    immediate: true, deep: true
  })
  selChange() {
    if (this.selectedMaterial === undefined) return;
    this.model.Tm = this.selectedMaterial['Tm'];
    if (this.selectedMaterial['id'] === 666){
      this.addNewMaterial = true;
    } else {
      this.addNewMaterial = false;
      getRequest(`${domain}/get_material/${this.selectedMaterial['id']}`, {}).then(res => {
        if (res['status']){
          this.model.W = res['material']['W']['value'];
          this.model.H = res['material']['H']['value'];
          this.model.L = res['material']['L']['value'];
          this.model.po = res['material']['po']['value'];
          this.model.c = res['material']['c']['value'];
          this.model.T_0 = res['material']['T_0']['value'];
          this.model.Tm = res['material']['Tm']['value'];
          this.model.mu_0 = res['material']['mu_0']['value'];
          this.model.b = res['material']['b']['value'];
          this.model.T_r = res['material']['T_r']['value'];
          this.model.n = res['material']['n']['value'];
          this.model.alf_u = res['material']['alf_u']['value'];
        }
      })
    }

  }

}
