export async function sendData(url: string, data: object) {
    const response = await fetch(url, {
        method: 'POST',
        mode: 'cors', // no-cors, *cors, same-origin
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify(data)
    });
    if (response.ok){
        return await response.json();
    } else {
        console.error(`Fetch err to ${url} with: ${response.status}`);
        return await response.json();
    }
}

export async function getRequest(url: string, urlParams: Record<string, string>){
    const response = await fetch(`${url}?${(new URLSearchParams(urlParams)).toString()}`);
    if (response.ok){
        return await response.json();
    } else {
        console.error(await response.json());
        return await response.json();
    }
}
