
































import {IQualityIndicator} from '@/interfaces/ChemData.ts';
import { PropType } from 'vue'

import {sendData} from '@/funcs/funcs'

import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop, Watch } from 'vue-property-decorator';
import {domain} from "@/consts/urls";
import ChemModel from "@/chem_model/chem_model";

@Component
export default class Tables extends Vue {

  @Prop() tableData!: Array<IQualityIndicator>;
  @Prop() model!: ChemModel;

  private tableDataSeparated: Array<Array<IQualityIndicator>> = this.separateTable(this.tableData, 20);

  private separateTable(table: Array<IQualityIndicator>, rows: number){
    let separated: Array<Array<IQualityIndicator>> = [];
    let counter = 0;
    let temp: Array<IQualityIndicator> = [];
    for (let i in table){
      counter++;
      if (counter === rows){
        separated.push(temp);
        counter = 0
        temp = []
      } else {
        temp.push(table[i]);
        if (parseInt(i) === table.length - 1){
          separated.push(temp);
        }
      }
    }
    return separated;
  }

  private async downloadTable(){
    console.log(this.model);
    let res = await sendData(`${domain}/create-xlsx`, {'table_data': this.tableData, 'model': this.model});
    let link = document.createElement("a");
    link.href = 'data:application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;base64,' + res['file'];
    link.style.visibility = "hidden";
    link.download = 'statistics.xlsx';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  }

  @Watch('tableData', {
    immediate: true, deep: true
  })
  tableDataChanged(newVal: Array<IQualityIndicator>, oldVal: Array<IQualityIndicator>) {
    this.tableData = newVal;
    this.tableDataSeparated = this.separateTable(this.tableData, 20)
  }
  // mounted() {
  // }
  // updated(){
  // }
}
