import {IQualityIndicator, ITandNu} from '@/interfaces/ChemData.ts'

export default class ChemModel {
    W: number = 0.17;
    H: number = 0.05;
    L: number = 6.6;
    Tm: string = 'Полипропилен';
    po: number = 890; // плотность
    c: number = 2300; // удельная теплоёмкость
    T_0: number = 175; // температура плавления
    V_u: number = 0.9; // скорость крышки канала
    T_u: number = 200; // температура крышки
    mu_0: number = 1550; // Коэффициент консистенции
    b: number = 0.015; // Температурный коэффициент взякости
    T_r: number = 180; // Температура приведения
    n: number = 0.4; // Индекс течения
    alf_u: number = 2000; // Коэффициент теплотдачи от крышки канала к материалу

    zStep: number = 0.1;

    // Calculated values
    F_w: number = this.calcF_w();
    gamma: number = this.calcGamma();
    q_n: number = this.calcQ_n();
    q_w: number = this.calcQ_w();
    Q: number = this.calcQ();

    T_last: number = 0;
    Nu_last: number = 0;

    calcF_w() : number {
        return 0.125 * Math.pow((this.H / this.W), 2) - 0.625 * (this.H / this.W) + 1;
    }

    calcGamma() : number {
        return this.V_u / this.H;
    }

    calcQ_n() : number {
        return this.H * this.W * this.mu_0 * Math.pow(this.gamma, this.n - 1);
    }

    calcQ_w() : number {
        return this.W * this.alf_u * (1/this.b + this.T_r - this.T_u);
    }

    calcQ() : number {
        return (this.W * this.H * this.V_u) / 2 * this.F_w * 3600;
    }

    calc_ae(z: number) : number {
        return ((this.b * this.q_n + this.W * this.alf_u) / (this.b * this.q_w)) *
            (1 - Math.exp((-this.b * this.q_w * z) / (this.Q / 3600 * this.c * this.po))) +
            Math.exp(this.b * (this.T_0 - this.T_r - (this.q_w * z) / (this.Q / 3600 * this.c * this.po)));
    }
    /**
     * Return T and Nu in object
     * @return {'Т' : <T value>, 'Nu': <Nu value>}
     */
    calcT_and_Nu(z: number) : ITandNu {
        let T: number = this.T_r + (1 / this.b) * Math.log(this.calc_ae(z));
        let Nu: number = this.mu_0 * Math.exp(-this.b * (T - this.T_r)) * Math.pow(this.gamma, this.n - 1);

        return {'T': T, 'Nu': Nu};
    }

    calcQualityIndicators(){
        this.updateModel();
        let QIs: Array<IQualityIndicator> = [];
        for (let z: number = 0; z <= this.L; z += this.zStep){
            let temp: ITandNu = this.calcT_and_Nu(z);
            QIs.push({
                'T': Math.round((temp['T'] + Number.EPSILON) * 100) / 100,
                'Nu': Math.round((temp['Nu'] + Number.EPSILON) * 100) / 100,
                'z': Math.round((z + Number.EPSILON) * 100) / 100
            })
            // console.log(`${typeof z} ${z} <= ${typeof this.L} ${this.L}; ${typeof this.zStep} ${this.zStep}`);
        }
        let last = this.calcT_and_Nu(this.L);
        this.T_last = (Math.round(last['T'] + Number.EPSILON) * 100) / 100;
        this.Nu_last = (Math.round(last['Nu'] + Number.EPSILON) * 100) / 100;
        return QIs;
    }

    updateModel(){
        this.F_w = this.calcF_w();
        this.gamma  = this.calcGamma();
        this.q_n = this.calcQ_n();
        this.q_w = this.calcQ_w();
        this.Q = this.calcQ();
    }
}
